import Vue from 'vue'

/* init root component */
import {initRequestCenterDisplay} from 'sdk/network/core/cloudjet_request'
import store from "../core/registry_vuex";
import {mapGetters} from "vuex";

const kpiapp = new Vue({
    el: "#kpiapp-library-container",
    mounted: function() {
        window.app = this
        setTimeout(function () {
            initRequestCenterDisplay()
        }, 100)
    },
    store,
    computed: {
        ...mapGetters([
            'userActor',
            'userViewed',
            'organization',
            'userActorSubordinates',
            // ...
        ])
    }
})
window.kpiAppLibrary = kpiapp