import NetworkNoti from 'sdk/network/notification'
// Mock network noti

// Import mutation
import {
  clearUnreadNotificationList,
  getInitialNotificationData,
  getLazyloadNotificationData
} from './mutations'

export const getInitialNotifiations = 'getInitialNotifiations';
export const markAsReadNotifications = 'markAsReadNotifications';
export const getLazyNotifications = 'getLazyNotifications';

export default {
  [getInitialNotifiations](state, userId) {
    NetworkNoti(userId).getInitialNotifiationData()
      .then((response) => {
        state.commit('getInitialNotificationData', response)
      })
      .catch(err => {
        console.log(err)
      })
  },

  [markAsReadNotifications](state, {uid, unreadNotificationLists}) {
    NetworkNoti(uid).updateUnreadNotifications(unreadNotificationLists)
      .then(() => {
        state.commit(clearUnreadNotificationList)
      })
      .catch(err => {
        console.log(err)
      })
  },

  [getLazyNotifications](state, nextLink) {
    NetworkNoti().getLazyLoadNotificationData(nextLink)
      .then(response => {
        state.commit(getLazyloadNotificationData, response)
      })
      .catch(err => {
        console.log(err)
      })
  }
}
