import config from './config'
import {
    buildPromise
} from 'sdk/network/core/network_builder'

export default (() => {
    // Tạo url: base + path
    const url = `${config.path}`

    return {
        url: url,
        getInitialProfile: () => {
            return buildPromise({
                httpMethod: 'GET',
                url: url
            })
        }
    }
})