import {
    cloudjetRequest
} from '../cloudjet_request'



// BuildPromise({url: 'xyz', parameter: 'GET'})
const buildPromise = (
    {
        httpMethod,
        url,
        data
    }
) => {

    return new Promise((resolve, reject) => {
        // Build ajax object
        const ajaxObject = {
            method: httpMethod,
            url: url,
            success: (data) => {
                resolve(data)
            },
            err: () => {
                reject()
            }
        }

        /**
         * Parameter when PUT/POST/DELETE
         */
        if (data != null) {
            ajaxObject.data = data
        }

        cloudjetRequest.ajax(ajaxObject)
    })
}
export {
    buildPromise
}