import config from './config'
import {
    buildPromise
} from 'sdk/network/core/network_builder'

export default (userId => {
    // Tạo url: base + path
    const url = `${config.path}/${userId}/`

    return {
        url: url,

        getInitialNotifiationData: () => {
            return buildPromise({
                httpMethod: 'GET',
                url
            })
        },

        getLazyLoadNotificationData: (url) => {
            return buildPromise({
                httpMethod: 'GET',
                url // Checkout variable cloaking / shadowing
            })
        },

        updateUnreadNotifications: (updateUnreadNotifications) => {
            return buildPromise({
                httpMethod: 'POST',
                url,
                data: JSON.stringify({unread_list: updateUnreadNotifications}),
            })
        }
    }
})