<template>
    <!--<notification-container-->
            <!--:notification-data="notificationData"-->
            <!--:on-lazy-load="getLazyLoadNotifications"-->
            <!--:on-mark-as-read="markAsRead"-->
            <!--:href="href"-->
    <!--&gt;-->
    <!--</notification-container>   -->
    <notification-container
        :notification-data="notificationData"
        :on-lazy-load="getLazyNotifications"
        :on-mark-as-read="markAsReadNotifications"
        :href="href"
    >
    </notification-container>
</template>

<script>
// Mutation List
import {
    getInitialNotifiations,
    getLazyNotifications,
    markAsReadNotifications
} from "./store/actions.js";
// Mutation List
import NotificationContainer from "./components/notification_container/notification_container";
import { mapGetters, mapActions } from "vuex";
import store from "./store";
export default {
  name: "main.vue",
  computed: {
    ...mapGetters({
      notificationData: "$notification/notificationData",
      href: "$notification/href",
      userActor: "userActor"
    })
  },
  components: {
    NotificationContainer
  },
  async mounted() {
    /* need to trigger global store initialized first, using synchronous loading style */
    await this.$store.dispatch("initialize");
    // await this.getInitialNotifiations(this.userActor.user);
    // this.$store.dispatch('$notification/getInitialNotifiations',this.userActor.user);
    this.dispatchGetInitialNotifiations(this.userActor.user)
    
  },
  created() {
    /* below should be bundle into a component builder*/
    const STORE_KEY = "$notification";
    // eslint-disable-next-line no-underscore-dangle
    if (!(STORE_KEY in this.$store._modules.root._children)) {
      this.$store.registerModule(STORE_KEY, store);
    }
    /* end this */
  },
  // can be wrong namespace
  methods: {
    ...mapActions({
      dispatchGetInitialNotifiations: `$notification/${getInitialNotifiations}`,
      dispatchGetLazyNotifications: `$notification/${getLazyNotifications}`,
      dispatchMarkAsReadNotifications: `$notification/${markAsReadNotifications}`
    }),
    getLazyNotifications (nextLink) {
      this.dispatchGetLazyNotifications(nextLink)
    },
    markAsReadNotifications (unreadNotificationLists) {
      this.dispatchMarkAsReadNotifications({
        uid: this.userActor.user,
        unreadNotificationLists
      })
    }
  }
};
</script>

