/**
 * Mutation là dạng string nên ta cần biến const để auto completet
 */
export const getInitialNotificationData = 'getInitialNotificationData'
export const clearUnreadNotificationList = 'clearUnreadNotificationList'
export const getLazyloadNotificationData = 'getLazyloadNotificationData'

export default {
  [getInitialNotificationData](state, notificationApiData) {
    state.notificationData = notificationApiData;
  },

  [clearUnreadNotificationList](state) {
    state.notificationData.unread_list = []
  },

  [getLazyloadNotificationData](state, notificationApiData) {
    state.notificationData.next = notificationApiData.next
    state.notificationData.unread_list = notificationApiData.unread_list
    state.notificationData.results = state.notificationData.results.concat(notificationApiData.results)
  }
}
